// MindMap.js
import React, { useEffect, useRef, useState } from 'react';
import { Graph, Shape } from '@antv/x6';
import { Stencil } from '@antv/x6-plugin-stencil';
import Hierarchy from '@antv/hierarchy';

/* ---------------- 1.  ---------------- */
const COLOR_POOL = [
  '#E8F5FF', '#FFF0E8', '#F0FFE8', '#F8E8FF', '#E8FFFF', '#FFE8F0', '#FFFFE8',
];

/* ---------------- 2.  + = ---------------- */
Graph.registerNode(
  'topic',
  {
    inherit: 'rect',
    markup: [
      { tagName: 'rect', selector: 'body' },
      { tagName: 'text', selector: 'label' },
    ],
    attrs: {
      body: {
        rx: 12, ry: 12,
        stroke: '${color}',
        fill: '${color}',
        strokeWidth: 1,
      },
      label: {
        fontSize: 16,
        fill: '#262626',
        whiteSpace: 'pre',
        textWrap: { width: '90%', height: '90%' },
      },
    },
  });

Graph.registerNode(
  'topic-child',
  {
    inherit: 'rect',
    markup: [
      { tagName: 'rect', selector: 'body' },
      { tagName: 'text', selector: 'label' },
      { tagName: 'path', selector: 'line' },
    ],
    attrs: {
      body: {
        rx: 12, ry: 12,
        stroke: '${color}',
        fill: '${color}',
        strokeWidth: 1,
      },
      label: {
        fontSize: 15,
        fill: '#262626',
        whiteSpace: 'pre',
        textVerticalAnchor: 'bottom',
        textWrap: { width: '90%', height: '90%' },
      },
      line: { stroke: '#d0d0d0', strokeWidth: 2, d: 'M 0 15 L 60 15' },
    },
  });

/* ---------------- 3.  ---------------- */
Graph.registerNode(
  'sticky-note',
  {
    inherit: 'rect',
    markup: [
      { tagName: 'rect', selector: 'body' },
      { tagName: 'text', selector: 'label' },
    ],
    attrs: {
      body: {
        rx: 16, ry: 16,
        stroke: 'none',
        fill: '#FFF9C4',
        strokeWidth: 0,
        filter: {
          name: 'dropShadow',
          args: {
            dx: 4,
            dy: 6,
            blur: 12,
            color: 'rgba(0, 0, 0, 0.15)',
          },
        },
      },
      label: {
        fontSize: 14,
        fill: '#333',
        refX: '50%',
        refY: '50%',
        textAnchor: 'middle',
        textVerticalAnchor: 'middle',
        textWrap: {
          width: -32,
          height: -32,
          ellipsis: false,
          breakWord: true,
        },
      },
    },
  },
);

/* ---------------- 4.  ---------------- */
Graph.registerEdge(
  'smooth-edge',
  {
    inherit: 'edge',
    attrs: {
      line: {
        stroke: '#d0d0d0',
        strokeWidth: 2,
        targetMarker: null,
      },
    },
    connector: {
      name: 'smooth',
      args: {
        direction: 'H',
      },
    },
  });

/* ---------------- 5.  ---------------- */
const treeData = {
  "nodes": [
    {
      "id": "root", 
      "child": [
        {
          "id": "1", 
          "child": [
            {"id": "1-1", "child": [], "label": "", "level": 2}, 
            {"id": "1-2", "child": [], "label": "", "level": 2}, 
            {"id": "1-3", "child": [], "label": "", "level": 2}, 
            {"id": "1-4", "child": [], "label": "'369'", "level": 2}
          ], 
          "label": "", 
          "level": 1
        }, 
        {
          "id": "2", 
          "child": [
            {"id": "2-1", "child": [], "label": "++", "level": 2}, 
            {"id": "2-2", "child": [], "label": "", "level": 2}, 
            {"id": "2-3", "child": [], "label": "", "level": 2}, 
            {"id": "2-4", "child": [], "label": "", "level": 2}, 
            {"id": "2-5", "child": [], "label": "", "level": 2}, 
            {"id": "2-6", "child": [], "label": "", "level": 2}
          ], 
          "label": "", 
          "level": 1
        }, 
        {
          "id": "3", 
          "child": [
            {"id": "3-1", "child": [], "label": "", "level": 2}, 
            {"id": "3-2", "child": [], "label": "", "level": 2}, 
            {"id": "3-3", "child": [], "label": "", "level": 2}, 
            {"id": "3-4", "child": [], "label": "", "level": 2}, 
            {"id": "3-5", "child": [], "label": "", "level": 2}, 
            {"id": "3-6", "child": [], "label": "", "level": 2}
          ], 
          "label": "", 
          "level": 1
        }
      ], 
      "label": "''", 
      "level": 0
    }
  ]
};

/* ---------------- 6. 7  +  ---------------- */
function calcSize(label) {
  const col = 7;
  const lines = [];
  for (let i = 0; i < label.length; i += col) lines.push(label.slice(i, i + col));
  const newLabel = lines.join('\n');
  const baseW = 160, baseH = 40, stepW = 20, stepH = 10;
  return {
    label: newLabel,
    width: Math.max(baseW, baseW + (Math.max(...lines.map((l) => l.length)) - 4) * stepW),
    height: baseH + (lines.length - 1) * stepH,
  };
}

let idSeq = 1, colorIndex = 0;

// 
function convertToHierarchyData(nodes) {
  if (!nodes || nodes.length === 0) return null;
  
  const rootNode = nodes[0]; // 
  return buildNode(rootNode);
}

function buildNode(node, parentId = null) {
  const id = node.id;
  const { label: newLabel, width, height } = calcSize(node.label);
  const color = COLOR_POOL[colorIndex % COLOR_POOL.length];
  colorIndex += 1;

  const result = {
    id,
    type: 'topic',
    label: newLabel,
    width,
    height,
    color,
  };

  // 
  if (node.child && node.child.length > 0) {
    result.children = node.child.map(childNode => buildNode(childNode, id));
  }

  return result;
}

// 
const hierarchyData = convertToHierarchyData(treeData.nodes);

/* ---------------- 7.  ---------------- */
function calcStickySize(text) {
  const lines = text.split('\n');
  const maxLineLength = Math.max(...lines.map(line => line.length));
  const lineCount = lines.length;
  
  const baseWidth = 160;
  const baseHeight = 120;
  
  const charWidth = 8;
  const lineHeight = 18;
  const padding = 20;
  
  const calculatedWidth = Math.max(baseWidth, maxLineLength * charWidth + padding);
  const calculatedHeight = Math.max(baseHeight, lineCount * lineHeight + padding);
  
  return { 
    width: calculatedWidth, 
    height: calculatedHeight 
  };
}

/* ---------------- 8.  Stencil ---------------- */
export default function MindMap() {
  const containerRef = useRef(null);
  const stencilContainerRef = useRef(null);
  const graphRef = useRef(null);

  useEffect(() => {
    if (!containerRef.current || !stencilContainerRef.current || !hierarchyData) return;

    const stencilWidth = 140;
    const availableWidth = window.innerWidth - stencilWidth;

    const graph = new Graph({
      container: containerRef.current,
      width: availableWidth,
      height: window.innerHeight,
      background: { color: 'rgba(255,255,255,0.1)' },
      interacting: { 
        nodeMovable: (cell) => {
          // 
          return cell.shape === 'sticky-note';
        },
      },
      selecting: { 
        enabled: true,
        rubberband: true,
        movable: true,
      },
      panning: {
        enabled: true,
        eventTypes: ['leftMouseDown', 'mouseWheel'],
      },
      mousewheel: {
        enabled: true,
        modifiers: [],
        factor: 1.05,
        minScale: 0.5,
        maxScale: 3,
      },
      autoResize: true,
      grid: { visible: false },
      scaling: { min: 0.5, max: 3 },
    });
    graphRef.current = graph;

    // 
    const hierarchy = Hierarchy.mindmap(hierarchyData, {
      direction: 'H',
      getHeight: (d) => d.height,
      getWidth: (d) => d.width,
      getHGap: () => 15,
      getVGap: () => 20,
      getSide: () => 'right',
    });

    const cells = [];
    const traverse = (item) => {
      const { data, children, x, y } = item;
      const node = graph.createNode({
        id: data.id,
        shape: data.type,
        x, y, width: data.width, height: data.height,
        label: data.label,
      });
      node.setAttrs({ body: { fill: data.color, stroke: data.color } });
      cells.push(node);

      children?.forEach((child) => {
        cells.push(
          graph.createEdge({
            shape: 'smooth-edge',
            source: { cell: data.id, anchor: { name: 'right' } },
            target: { cell: child.id, anchor: { name: 'left' } },
          })
        );
        traverse(child);
      });
    };
    traverse(hierarchy);
    graph.resetCells(cells);

    // 
    graph.scale(0.6);
    
    const contentBounds = graph.getContentBBox();
    const graphCenterX = availableWidth / 2;
    const graphCenterY = window.innerHeight / 2;
    
    const targetX = graphCenterX - (contentBounds.x + contentBounds.width / 2) * 0.6;
    const targetY = graphCenterY - (contentBounds.y + contentBounds.height / 2) * 0.6;
    
    graph.translate(targetX, targetY);

    // 
    const STENCIL_STICKY_SIZE = { width: 80, height: 80 };
    const CANVAS_STICKY_SIZE = { width: 160, height: 120 };

    // 
    const createStickyTemplate = () => {
      return graph.createNode({
        shape: 'sticky-note',
        width: STENCIL_STICKY_SIZE.width,
        height: STENCIL_STICKY_SIZE.height,
        position: { x: 20, y: 20 },
        attrs: {
          label: {
            text: '',
          },
          body: {
            fill: '#FFF9C4',
          },
        },
      });
    };

    //  Stencil 
    const stencil = new Stencil({
      target: graph,
      stencilGraphWidth: 120,
      stencilGraphHeight: 120,
      groups: [
        {
          name: 'group1',
          title: '',
          collapsable: false,
        },
      ],
      layout: (model, group) => {
        const cells = model.getCells();
        cells.forEach((cell, index) => {
          cell.setPosition(20, 20);
        });
      },
      getDragNode: (node) => {
        return node.clone().resize(STENCIL_STICKY_SIZE.width, STENCIL_STICKY_SIZE.height);
      },
    });

    stencilContainerRef.current.appendChild(stencil.container);

    // 
    const stickyTemplate = createStickyTemplate();
    stencil.load([stickyTemplate], 'group1');

    // 
    const applyStencilStyles = () => {
      try {
        // 
        const titles = document.querySelectorAll('.x6-widget-stencil-title');
        titles.forEach(title => {
          title.style.display = 'none';
        });

        //  Stencil 
        const stencilElement = document.querySelector('.x6-widget-stencil');
        if (stencilElement) {
          stencilElement.style.overflow = 'visible';
          stencilElement.style.background = 'transparent';
          stencilElement.style.border = 'none';
        }

        //  Stencil 
        const stencilContent = document.querySelector('.x6-widget-stencil-content');
        if (stencilContent) {
          stencilContent.style.overflow = 'visible';
          stencilContent.style.background = 'transparent';
          stencilContent.style.border = 'none';
          stencilContent.style.minHeight = '120px';
        }

        //  Stencil 
        const stencilGraph = document.querySelector('.x6-widget-stencil-graph');
        if (stencilGraph) {
          stencilGraph.style.overflow = 'visible';
          stencilGraph.style.background = 'transparent';
          stencilGraph.style.border = 'none';
        }

        // 
        const nodes = document.querySelectorAll('.x6-widget-stencil .x6-node');
        nodes.forEach(node => {
          node.style.overflow = 'visible';
          node.style.background = 'transparent';
          node.style.border = 'none';
          node.style.boxShadow = 'none';
          
          const rect = node.querySelector('rect');
          if (rect) {
            rect.style.fill = '#FFF9C4';
            rect.style.stroke = 'none';
            rect.style.strokeWidth = '0';
            rect.style.rx = '16px';
            rect.style.ry = '16px';
            rect.style.cursor = 'grab';
            rect.style.filter = 'drop-shadow(4px 6px 12px rgba(0,0,0,0.15))';
            rect.setAttribute('width', STENCIL_STICKY_SIZE.width);
            rect.setAttribute('height', STENCIL_STICKY_SIZE.height);
          }
          
          const text = node.querySelector('text');
          if (text) {
            text.style.fill = 'rgb(51, 51, 51);';
            text.style.fontSize = '14px';
            text.style.textAnchor = 'middle';
          }

          const dragHandle = node.querySelector('.x6-node-drag-handle');
          if (dragHandle) {
            dragHandle.style.display = 'block';
            dragHandle.style.visibility = 'visible';
            dragHandle.style.opacity = '1';
            dragHandle.style.cursor = 'grab';
            
            const circle = dragHandle.querySelector('circle');
            if (circle) {
              circle.style.fill = '#f0f0f0';
              circle.style.stroke = '#d0d0d0';
              circle.style.strokeWidth = '1px';
              circle.style.r = '8px';
              circle.style.cursor = 'grab';
            }
          }
        });

        console.log('Stencil ');
      } catch (error) {
        console.warn(':', error);
      }
    };

    // 
    const applyStylesWithRetry = (retryCount = 0) => {
      if (retryCount > 5) return;
      
      setTimeout(() => {
        applyStencilStyles();
        
        const hiddenNodes = document.querySelectorAll('.x6-widget-stencil .x6-node[style*="display: none"]');
        const invisibleNodes = document.querySelectorAll('.x6-widget-stencil .x6-node[style*="visibility: hidden"]');
        
        if (hiddenNodes.length > 0 || invisibleNodes.length > 0) {
          console.log('');
          applyStylesWithRetry(retryCount + 1);
        }
      }, 200 * (retryCount + 1));
    };

    // 
    applyStylesWithRetry();

    //  DOM 
    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        if (mutation.type === 'childList') {
          mutation.addedNodes.forEach((node) => {
            if (node.nodeType === 1 && node.classList && node.classList.contains('x6-node')) {
              applyStencilStyles();
            }
          });
        }
      });
    });

    if (stencilContainerRef.current) {
      observer.observe(stencilContainerRef.current, {
        childList: true,
        subtree: true
      });
    }

    // 
    graph.on('cell:added', ({ cell }) => {
      if (cell.shape === 'sticky-note') {
        cell.setProp('movable', true);
        
        setTimeout(() => {
          cell.resize(CANVAS_STICKY_SIZE.width, CANVAS_STICKY_SIZE.height);
          if (!cell.attr('label/text') || cell.attr('label/text') === '') {
            cell.attr('label/text', '');
          }
        }, 100);
      }
    });

    // 
    graph.on('cell:dblclick', ({ cell }) => {
      if (cell.shape === 'sticky-note') {
        cell.addTools([
          {
            name: 'editor',
            args: {
              event: 'cell:dblclick',
            },
          },
        ]);
      }
    });

    // 
    graph.on('cell:change:attrs', ({ cell }) => {
      if (cell.shape === 'sticky-note') {
        const text = cell.attr('label/text') || '';
        if (text && text !== '') {
          const { width, height } = calcStickySize(text);
          cell.resize(
            Math.max(CANVAS_STICKY_SIZE.width, width),
            Math.max(CANVAS_STICKY_SIZE.height, height)
          );
        }
      }
    });

    // 
    const el = containerRef.current;
    let touchStartPoint = null;
    let isTouchPanning = false;
    let touchStartTime = 0;
    let lastTouchMoveTime = 0;
    let touchMoved = false;
    let initialPinchDistance = null;

    const onTouchStart = (e) => {
      if (e.touches.length === 1) {
        touchStartPoint = { 
          x: e.touches[0].clientX, 
          y: e.touches[0].clientY 
        };
        touchStartTime = Date.now();
        touchMoved = false;
      } else if (e.touches.length === 2) {
        //  - 
        const touch1 = e.touches[0];
        const touch2 = e.touches[1];
        initialPinchDistance = Math.sqrt(
          Math.pow(touch2.clientX - touch1.clientX, 2) +
          Math.pow(touch2.clientY - touch1.clientY, 2)
        );
        isTouchPanning = true;
      }
    };

    const onTouchMove = (e) => {
      const now = Date.now();
      
      if (e.touches.length === 1 && touchStartPoint) {
        // 
        const touch = e.touches[0];
        const dx = touch.clientX - touchStartPoint.x;
        const dy = touch.clientY - touchStartPoint.y;
        
        const distance = Math.sqrt(dx * dx + dy * dy);
        
        if (distance > 5) {
          touchMoved = true;
          
          //  X6 
          if (now - lastTouchMoveTime > 16) {
            graph.translateBy(dx, dy);
            touchStartPoint = { x: touch.clientX, y: touch.clientY };
            lastTouchMoveTime = now;
          }
        }
      } else if (e.touches.length === 2 && initialPinchDistance) {
        // 
        const touch1 = e.touches[0];
        const touch2 = e.touches[1];
        
        const currentDistance = Math.sqrt(
          Math.pow(touch2.clientX - touch1.clientX, 2) +
          Math.pow(touch2.clientY - touch1.clientY, 2)
        );
        
        const scaleChange = currentDistance / initialPinchDistance;
        const currentScale = graph.zoom();
        const newScale = Math.max(0.5, Math.min(3, currentScale * scaleChange));
        
        graph.zoomTo(newScale);
        initialPinchDistance = currentDistance;
      }
    };

    const onTouchEnd = (e) => {
      touchStartPoint = null;
      isTouchPanning = false;
      touchMoved = false;
      initialPinchDistance = null;
    };

    // 
    let mouseStartPoint = null;
    let isMousePanning = false;
    let mouseMoved = false;

    const onMouseDown = (e) => {
      if (e.button === 0) { // 
        mouseStartPoint = { x: e.clientX, y: e.clientY };
        isMousePanning = true;
        mouseMoved = false;
      }
    };

    const onMouseMove = (e) => {
      if (!mouseStartPoint || !isMousePanning) return;
      
      const dx = e.clientX - mouseStartPoint.x;
      const dy = e.clientY - mouseStartPoint.y;
      
      if (Math.abs(dx) > 5 || Math.abs(dy) > 5) {
        mouseMoved = true;
        
        //  X6 
        graph.translateBy(dx, dy);
        mouseStartPoint = { x: e.clientX, y: e.clientY };
      }
    };

    const onMouseUp = (e) => {
      mouseStartPoint = null;
      isMousePanning = false;
      mouseMoved = false;
    };

    // 
    el.addEventListener('touchstart', onTouchStart, { passive: true });
    el.addEventListener('touchmove', onTouchMove, { passive: true });
    el.addEventListener('touchend', onTouchEnd, { passive: true });

    el.addEventListener('mousedown', onMouseDown);
    el.addEventListener('mousemove', onMouseMove);
    el.addEventListener('mouseup', onMouseUp);

    // 
    const handleResize = () => {
      const newAvailableWidth = window.innerWidth - stencilWidth;
      graph.resize(newAvailableWidth, window.innerHeight);
      
      const newGraphCenterX = newAvailableWidth / 2;
      const newGraphCenterY = window.innerHeight / 2;
      
      const newTargetX = newGraphCenterX - (contentBounds.x + contentBounds.width / 2) * 0.6;
      const newTargetY = newGraphCenterY - (contentBounds.y + contentBounds.height / 2) * 0.6;
      
      graph.translate(newTargetX, newTargetY);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      graph.dispose();
      stencil.dispose();
      observer.disconnect();
      el.removeEventListener('touchstart', onTouchStart);
      el.removeEventListener('touchmove', onTouchMove);
      el.removeEventListener('touchend', onTouchEnd);
      el.removeEventListener('mousedown', onMouseDown);
      el.removeEventListener('mousemove', onMouseMove);
      el.removeEventListener('mouseup', onMouseUp);
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <div style={{ 
      width: '100vw', 
      height: '100vh', 
      overflow: 'hidden', 
      display: 'flex',
      background: '#f5f5f5'
    }}>
      {/*  */}
      <div style={{ 
        width: '140px',
        height: '100%', 
        background: 'white',
        boxSizing: 'border-box',
        display: 'flex',
        flexDirection: 'column',
        borderRight: '1px solid rgba(225,225,225,0.8)',
        overflow: 'hidden'
      }}>
        <div 
          ref={stencilContainerRef} 
          style={{ 
            flex: 1,
            overflow: 'hidden',
            position: 'relative'
          }} 
        />
      </div>

      {/*  */}
      <div style={{ 
        flex: 1, 
        height: '100%',
        background: 'white',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <div 
          ref={containerRef} 
          style={{ 
            width: '100%',
            height: '100%'
          }} 
        />
      </div>
    </div>
  );
}